.TermsCheckbox { width: 100%;text-align: center; margin: 0; margin-bottom: 0px; font-family: Open Sans; font-size: 13px; margin-bottom: 4px; }
.TermsCheckbox label {padding-left: 31px; font-weight: 600; position: relative; display: inline-block; padding: 0 0 0 2em; margin-right: 0; height: 1.5em; line-height: 14px; cursor: pointer; text-align: left; font-size: 11px; }
.TermsCheckbox label::before,
.TermsCheckbox label::after {position: absolute; top: 0; left: 0; display: block; width: 17px; height: 17px; }
.TermsCheckbox label::before {content: " "; border: 2px solid #212529; -webkit-border-radius: 3px; -moz-border-radius: 3px; -ms-border-radius: 3px; -o-border-radius: 3px; border-radius: 3px; }
.TermsCheckbox input[type="checkbox"]{position: absolute; opacity: 0; z-index: 999; margin: 0; }
.TermsCheckbox input[type="checkbox"] + label::after {content: "\2714"; color: #FFF; line-height: 1.3; text-align: center; border: none; }
.TermsCheckbox input[type="checkbox"]:checked + label::before {background:#018a3d; border-color:#018a3d ; }
.TermsCheckbox input[type="checkbox"] + label::after{-webkit-transform: scale(0); -moz-transform: scale(0); -ms-transform: scale(0); -o-transform: scale(0); transform: scale(0); }
.TermsCheckbox input[type="checkbox"]:checked + label::after{-webkit-transform: scale(1); -moz-transform: scale(1); -ms-transform: scale(1); -o-transform: scale(1); transform: scale(1); }
.TermsCheckbox label a{color: #00401f;}
