.full-width-modal-section.modal-dialog { max-width: 700px; }
.categoryAdd {background: #ffc107; color: #fff; font-size: 12px; box-shadow: none; position: absolute; right: 0; top: 0 } 
.categoryList .MuiToolbar-regular{    min-height: auto;}

.categoryList .MuiTableCell-body p{margin-bottom: 0;} 
h6.MuiTypography-root {font-size: 16px; color: #000; font-weight: bold !important; }
.search-filter {background: #f6f9fc; padding: 10px; margin-bottom: 20px; border-radius: 2px; }
.search-filter .form-group{margin-bottom: 0;}
.search-filter .form-group label {margin-bottom: 0; font-size: 12px; font-weight: 600; color: #000; }
.search-filter .form-group .form-control {border: 1px solid #cdd4db; font-size: 13px; outline: none; }
.search-filter button.search-btn{background: #ffc107 !important; color: #fff; font-weight: bold; border: none; box-shadow: none; }
.filter-button-section {padding-top: 15px; }

button.btn{font-size: 14px;}

.search-c-change{background: #1c75bc; border:1px solid #1c75bc; color: #fff;}
.reset-c-change{background: #18c99f; border:1px solid #18c99f; color: #fff;}

.search-c-change:hover{background: #2d87cf; border:1px solid #2d87cf !important; color: #fff;}
.reset-c-change:hover{background: #27dfb4; border:1px solid #27dfb4 !important; color: #fff;}

.search-c-change:active{background: #2d87cf; border:1px solid #2d87cf!important; color: #fff;}
.reset-c-change:active{background: #27dfb4; border:1px solid #27dfb4 !important; color: #fff;}