.oep-challenge {
    padding: 8px 10px;
    color: #fff !important;
    font-weight: bold;
    margin-left: 10px;
}

.btn:hover, .btn:focus, .btn:active, .btn-ye:hover {
    outline: 0;
    box-shadow: 0 5px 11px 0 rgba(0,0,0,0.18), 0 4px 15px 0 rgba(0,0,0,0.15);
    text-decoration: none !important;
}
.pdf-download-button {
	background: #fff;
    padding: 5px;
    width: 150px;
    height: 30px;
    border: 1px solid #001540;
    text-align: center;
    border-radius: 5px;
    margin: 0 2px;
    color: #001540;
    display: inline-block;
}
.pdf-toolbar-padding {
	padding: 15px;
}

.Search-filter .pdf-toolbar {
    position: absolute;
    top: -59px;
    right: 0px;
}